import { createSelector } from 'reselect';
import { RootState } from '../store';

const getNotes = (state: RootState) => state.notes.items;
const getStatistics = (state: RootState) => state.notes.statistics;

const getMemoNotes = createSelector([getNotes], notes => notes);

const getActiveNotes = createSelector([getNotes], notes => {
  return notes.filter(note => !note.archived);
});

const getArchivedNotes = createSelector([getNotes], notes => {
  return notes.filter(note => note.archived);
});

const getSortedStatistics = createSelector([getStatistics], statistics => {
  return [...statistics].sort((a, b) => a.category.localeCompare(b.category));
});

const notesSelectors = {
  getMemoNotes,
  getActiveNotes,
  getArchivedNotes,
  getSortedStatistics
};

export default notesSelectors;
