import { useAppDispatch, useAppSelector } from '../redux/hooks';
import { useEffect } from 'react';
import notesSelectors from '../redux/Notes/notesSelectors';
import Table from '../components/Table/Table';
import notesOperations from '../redux/Notes/notesOperations';

const SummaryView = () => {
  const notes = useAppSelector(notesSelectors.getMemoNotes);
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(notesOperations.fetchStatistics());
  }, [dispatch, notes]);

  const summaryData = useAppSelector(notesSelectors.getSortedStatistics);
  return (
    <div className="container">
      <Table summaryTable summaryData={summaryData} />
    </div>
  );
};

export default SummaryView;
