import CATEGORIES from '../../data/categories';
import Button from '../Button/Button';
import s from './EditorForm.module.scss';

interface IProps {
  note: {
    name: string;
    category: string;
    content: string;
  };
  handleSubmit: (e: React.FormEvent<HTMLFormElement>) => void;
  handleChange: (
    e:
      | React.ChangeEvent<HTMLInputElement>
      | React.ChangeEvent<HTMLSelectElement>
      | React.ChangeEvent<HTMLTextAreaElement>
  ) => void;
  onClose: () => void;
}

const EditorForm = ({ note, handleSubmit, handleChange, onClose }: IProps) => {
  return (
    <form className={s.form} onSubmit={handleSubmit} autoComplete="off">
      <label className={s.label} htmlFor="name">
        Name
      </label>
      <input
        id="name"
        name="name"
        type="text"
        onChange={handleChange}
        value={note.name}
        required
      />
      <label className={s.label} htmlFor="category">
        Category
      </label>
      <select
        id="category"
        name="category"
        onChange={handleChange}
        // value={note.category ? note.category : 'default'}
        required
      >
        <option defaultValue={note.category ? note.category : 'default'}>
          {note.category ? note.category : ' Choose category'}
        </option>
        {Object.values(CATEGORIES)
          .filter(value => value !== note.category)
          .map(value => {
            return (
              <option key={value} value={value}>
                {value}
              </option>
            );
          })}
      </select>
      <label className={s.label} htmlFor="content">
        Content
      </label>
      <textarea
        id="content"
        name="content"
        onChange={handleChange}
        value={note.content}
        required
      ></textarea>
      <div className={s.buttonContainer}>
        <Button type="button" onClick={onClose} text="Dismiss" />
        <Button type="submit" text="Save" />
      </div>
    </form>
  );
};

export default EditorForm;
