import { createSlice, combineReducers } from '@reduxjs/toolkit';
import notesOperations from './notesOperations';
import INote from '../../interfaces/Note.interface';
import ISummary from '../../interfaces/Summary.interface';

const initialItems: INote[] = [];
const initialStats: ISummary[] = [];

const items = createSlice({
  name: 'items',
  initialState: initialItems,
  reducers: {},
  extraReducers: builder => {
    builder.addCase(notesOperations.fetchNotes.fulfilled, (_, { payload }) => {
      return payload;
    });
    builder.addCase(
      notesOperations.createNote.fulfilled,
      (state, { payload }) => {
        state.push(payload);
      }
    );
    builder.addCase(
      notesOperations.deleteNote.fulfilled,
      (state, { payload }) => {
        return state.filter(note => note._id !== payload.note._id);
      }
    );
    builder.addCase(
      notesOperations.updateNote.fulfilled,
      (state, { payload }) => {
        return [...state.filter(note => note._id !== payload._id), payload];
      }
    );
    builder.addCase(
      notesOperations.handleArchive.fulfilled,
      (state, { payload }) => {
        return [...state.filter(note => note._id !== payload._id), payload];
      }
    );
  }
});

const statistics = createSlice({
  name: 'statistics',
  initialState: initialStats,
  reducers: {},
  extraReducers: builder => {
    builder.addCase(
      notesOperations.fetchStatistics.fulfilled,
      (_, { payload }) => {
        return payload;
      }
    );
  }
});

export default combineReducers({
  items: items.reducer,
  statistics: statistics.reducer
});
