import { useCallback, useEffect, useState } from 'react';
import { useAppDispatch, useAppSelector } from '../redux/hooks';
import notesSelectors from '../redux/Notes/notesSelectors';
import Table from '../components/Table/Table';
import Button from '../components/Button/Button';
import ModalWindow from '../components/ModalWindow/ModalWindow';
import NoteEditor from '../components/NoteEditor/NoteEditor';
import notesOperations from '../redux/Notes/notesOperations';

const initialNoteDataToEdit = {
  _id: '',
  name: '',
  category: '',
  content: ''
};

const NotesView = () => {
  const [showArchivedNotes, setShowArchivedNotes] = useState(false);
  const [showModal, setSHowModal] = useState(false);
  const [isEditMode, setEditMode] = useState(false);
  const [noteDataToEdit, setNoteDataToEdit] = useState(initialNoteDataToEdit);

  const dispatch = useAppDispatch();
  useEffect(() => {
    dispatch(notesOperations.fetchNotes());
  }, [dispatch]);

  const activeNotes = useAppSelector(notesSelectors.getActiveNotes).sort(
    (a, b) => Date.parse(b.createdAt) - Date.parse(a.createdAt)
  );

  const archivedNotes = useAppSelector(notesSelectors.getArchivedNotes).sort(
    (a, b) => Date.parse(b.createdAt) - Date.parse(a.createdAt)
  );

  const handleArchivedNotes = useCallback(
    () => setShowArchivedNotes(!showArchivedNotes),
    [showArchivedNotes]
  );

  const openEditor = () => {
    setSHowModal(true);
  };

  const closeEditor = () => {
    disableEditMode();
    setSHowModal(false);
  };

  const enableEditMode = (
    _id: string,
    name: string,
    category: string,
    content: string
  ) => {
    openEditor();
    setEditMode(true);
    setNoteDataToEdit(prev => ({
      ...prev,
      _id,
      name,
      category,
      content
    }));
  };

  const disableEditMode = () => {
    if (!isEditMode) return;
    setEditMode(false);
    setNoteDataToEdit(initialNoteDataToEdit);
  };

  const visibleNotes = showArchivedNotes ? archivedNotes : activeNotes;

  return (
    <div className="container">
      <Table
        noteTable
        showArchived={showArchivedNotes}
        handleArchivedNotes={handleArchivedNotes}
        notes={visibleNotes}
        enableEditMode={enableEditMode}
      />
      {!showArchivedNotes && (
        <Button
          type="button"
          text="Create Note"
          onClick={openEditor}
          className="btn--toRight"
        />
      )}

      {showModal && (
        <ModalWindow onClose={closeEditor}>
          <NoteEditor
            isEditMode={isEditMode}
            closeEditor={closeEditor}
            noteId={noteDataToEdit._id}
            noteName={noteDataToEdit.name}
            noteCategory={noteDataToEdit.category}
            noteContent={noteDataToEdit.content}
          />
        </ModalWindow>
      )}
    </div>
  );
};

export default NotesView;
