import axios from 'axios';
import { createAsyncThunk } from '@reduxjs/toolkit';
import IUpdateNoteData from '../../interfaces/UpdateNoteData.interface';

// const deleteAllNotes = createAction('notes/deleteAllNotes');

axios.defaults.baseURL = 'https://rkruk-notes-server-ts.herokuapp.com';

interface IUpdateCredentials {
  noteId: string;
  editedNote?: IUpdateNoteData;
  status?: {
    archived: boolean;
  };
}

const fetchNotes = createAsyncThunk(
  'notes/fetchNotes',
  async (_, { rejectWithValue }) => {
    try {
      const { data } = await axios.get('/notes');
      return data;
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

const createNote = createAsyncThunk(
  'notes/createNote',
  async (note: IUpdateNoteData, { rejectWithValue }) => {
    try {
      const { data } = await axios.post('/notes', note);
      return data;
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

const deleteNote = createAsyncThunk(
  'notes/deleteNote',
  async (_id: string, { rejectWithValue }) => {
    try {
      const { data } = await axios.delete(`notes/${_id}`);
      return data;
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

const updateNote = createAsyncThunk(
  'notes/updateNote',
  async (credentials: IUpdateCredentials, { rejectWithValue }) => {
    try {
      const { data } = await axios.patch(
        `notes/${credentials.noteId}`,
        credentials.editedNote
      );
      return data;
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

const handleArchive = createAsyncThunk(
  'notes/handleArchive',
  async (credentials: IUpdateCredentials, { rejectWithValue }) => {
    try {
      const { data } = await axios.patch(
        `/notes/${credentials.noteId}/archived`,
        credentials.status
      );
      return data;
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

const fetchStatistics = createAsyncThunk(
  'notes/fetchStatistics',
  async (_, { rejectWithValue }) => {
    try {
      const { data } = await axios.get('/notes/stats');
      return data;
    } catch (error: any) {
      return rejectWithValue(error.message);
    }
  }
);

const notesOperations = {
  fetchNotes,
  // deleteAllNotes,
  createNote,
  deleteNote,
  updateNote,
  handleArchive,
  fetchStatistics
};

export default notesOperations;
